@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom fonts & colors */
@layer base {
  body {
    @apply bg-gray-100 text-gray-800 font-sans;
  }
  h1,
  h2,
  h3 {
    @apply font-bold text-gray-900;
  }
}

/* Smooth transitions */
* {
  @apply transition-colors duration-200;
}

/* Custom container override */
.container {
  @apply max-w-6xl mx-auto px-4 py-6;
}

/* Buttons */
.btn {
  @apply px-4 py-2 rounded-md font-medium text-white bg-blue-600 hover:bg-blue-700 shadow;
}

.btn-red {
  @apply px-3 py-1 rounded text-white bg-red-500 hover:bg-red-600 shadow;
}

.input {
  @apply w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500;
}

/* Card styles */
.card {
  @apply bg-white rounded-lg shadow p-4 hover:shadow-lg;
}

/* Alert colors */
.alert-success {
  @apply bg-green-100 text-green-800 border-l-4 border-green-500 p-4 rounded;
}

.alert-danger {
  @apply bg-red-100 text-red-800 border-l-4 border-red-500 p-4 rounded;
}
